import React, { Component } from "react";
import ServicesCreator from "./ServicesCreator";
import axios from "axios";
import './Style.css';

class Services extends Component {
  constructor() {
    super();
    this.state = {
      servicesData: [],
    };
  }

  componentDidMount() {
    axios.get("json/services.json").then((res) => {
      this.setState({ servicesData: res.data });
    });

  }

  componentDidUpdate(){
    if(this.state.servicesData.length === 1){
      var x = window.matchMedia("(min-width: 701px)")
      if(x.matches){
        const a = document.getElementById('service_1');
        a.style.gridColumn = '1/-1';
        a.style.width = '390px';
      }
    }
  }

  //fill with info

  servicesFiller() {
    let { servicesData } = this.state;
    return servicesData.map((data) => {
      return (
        <ServicesCreator logo={data.logo} title={data.title} info={data.info} id={data.id} key={data.id}/>
      );
    });
  }

  render() {
    return (
      <section id="services">
        <div className="row">
          <div className='midLine'></div>
          <h1 className="titleFontSize">Our Services</h1>
          <p className="normalFontSize">
            If no one hates it, no one really loves it.
          </p>
          {this.servicesFiller()}
        </div>
      </section>
    );
  }
}

export default Services;
