import React from "react";
import {ShowInfoOnHoverAndClick} from '../Hooks';

function ProjectsCreator(props) {

  ShowInfoOnHoverAndClick("projects__container","projects__infoAndLink");


  return (<div className="projects__container" id={props.id}>
    <div className="projects__infoAndLink">
      <h1>{props.info}</h1>
      <a className='btn' href={props.link} rel="noreferrer" target='_blank'>Visit</a>
    </div>
    <img src={props.img} alt="project"/>
  </div>);
}

export default ProjectsCreator;
