import React from "react";
import {ShowInfoOnHoverAndClick} from '../Hooks';

function TeamCreator(props) {

  ShowInfoOnHoverAndClick("team__container","team_info");

  return (<div className="team__container" id={props.id}>
    <div className='team_info'>
      <h3 className='team_name'>{props.name}</h3>
      <h6 className='team_job'>{props.job}</h6>
      <ul>
        <li >
          <a rel="noreferrer" target='_blank' className='team_socialLink' href={props.facebook}>
            <i className={props.facebookLogo}></i>
          </a>
        </li>
        <li >
          <a rel="noreferrer" target='_blank' className='team_socialLink' href={props.behance}>
            <i className={props.behanceLogo}></i>
          </a>
        </li>
        <li >
          <a rel="noreferrer" target='_blank' className='team_socialLink' href={props.instagram}>
            <i className={props.instagramLogo}></i>
          </a>
        </li>
      </ul>
    </div>
    <img src={props.img} alt='team' className='team__img'/>
  </div>);

}

export default TeamCreator;
