import $ from 'jquery';
import { useEffect } from 'react';
export const ShowInfoOnHoverAndClick = function(containerClass ,divToShow){
    useEffect(() => {
        //get all projects container
        var elements = document.getElementsByClassName(containerClass);

        //show elements
        var show = function(event) {
          var a = document.querySelector(`#${event.currentTarget.id} .${divToShow}`);
          $(a).fadeIn(300);
          $(a).css('display', 'grid');
        };

        var hide = function(event) {
          var a = document.querySelector(`#${event.currentTarget.id} .${divToShow}`);
          $(a).fadeOut(300);

        };

        //hide elements
        $(document).on('touchstart', (event) => {
          if (!$(event.target).closest(containerClass).length) {
            $(`.${divToShow}`).fadeOut(300);
          }
        });

        for (var i = 0; i < elements.length; i++) {
          elements[i].addEventListener('click', show, false);
          elements[i].addEventListener('mouseenter', show, false);
          elements[i].addEventListener('mouseleave', hide, false);
        }

      },);
};