import React from "react";
import {ShowInfoOnHoverAndClick} from '../Hooks';

export default function SVTC(props) {

  ShowInfoOnHoverAndClick("team__container","team_info");

  return (
    <div className="team__container SV_team__container" id={props.id}>
      <div className="team_info">
        <h3 className="team_name">{props.name}</h3>
        <ul>
         <li><a className='team_socialLink' rel='noreferrer' target='_blank' href={props.link}><i className='bx bxl-youtube'></i></a></li>
        </ul>
      </div>
      <img src={props.img} alt="team" className="team__img" />
    </div>
  );
}
