import React, { Component } from "react";
import { Link } from "react-router-dom";
import './Style.css'
import Hero from "../Hero/Index";
import { HeroNavImg } from "../Hero/Style";
import NavMobOpen from "./NavMobOpen";

class NavBar extends Component {
  constructor(props){
    super(props);
    this.setChanged = this.setChanged.bind(this);
    this.state = {
      navMobOpen : false
    }
  }
  setChanged =()=> {
    this.setState({ navMobOpen: false });
  }



  render() {
    if(this.state.navMobOpen === false){
        return(
          <HeroNavImg id="navbar">
        <div className="row navAndHero">
          <nav className='navbar__pc'>
          <div className="navbar__logo">
            <a href="//imweb.netlify.app">IM WEB</a>
          </div>

          <div className="navbar__links">
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <a href="#services">Services</a>
              </li>
              <li>
                <a href="#projects">Projects</a>
              </li>
              <li>
                <a href="#team">Team</a>
              </li>
              <li>
                <Link to="/syrian_vlogers">Syrian vloggers</Link>
              </li>
              <li>
                <a href="#contact">Contact</a>
              </li>
              <li>
                <a href='//facebook.com'>
                  <i className="bx bxl-facebook"></i>
                </a>
              </li>
              <li>
                <a href='//instagram.com'>
                  <i className="bx bxl-instagram"></i>
                </a>
              </li>
              <li>
                <a href='http://behance.net/ibrahimmaded'>
                  <i className="bx bxl-behance"></i>
                </a>
              </li>
            </ul>
          </div>
          </nav>

          <nav className='navbar__mobile'>
            <div className="navbar__logo">
              <a href="//imweb.netlify.app">IM WEB</a>
            </div>

            <div className="navbar__mobile--menu" onClick={() => this.setState({navMobOpen:true})}>
              <a href='/#'>
                <i className="bx bx-menu"></i>
              </a>
            </div>
          </nav>
          <Hero/>
        </div>
      </HeroNavImg>
        );
    }else{
      return(
        <NavMobOpen setChanged={this.setChanged}/>
        );
    }
  }
}

export default NavBar;
