import React, { Component } from "react";
import ErrorNav from "../NavBar/ErrorNav";
import Footer from "../Footer/Index";
import SVTC from "./SVTC";
import axios from "axios";
import './Style.css';

export default class SyrianVloggers extends Component {

    constructor(){
        super();
        this.state={teamData : []}
    }

    componentDidMount() {
        axios.get("json/syrianVloggers.json").then((res) => {
          this.setState({ teamData: res.data });
        });

    }

    //fill with info

  teamFiller() {
      const {teamData} = this.state;
    return teamData.map((data) => {
      return (
        <SVTC
          img={data.img ? data.img : `${process.env.PUBLIC_URL}/images/team/team_default.png`}
          id={data.id}
          name={data.name}
          link={data.link ? data.link : '//youtube.com'}
          key={data.id}
        />
      );
    });
  }
  render() {
    return (
      <>
        <ErrorNav />
        <main>
          <section id="syrianvloggers">
            <div className="row">
              <h1 className="titleFontSize">Syrian vloggers team</h1>
              <p className="normalFontSize">
                If no one hates it, no one really loves it.
              </p>
              {this.teamFiller()}
            </div>
          </section>
        </main>
        <Footer/>
      </>
    );
  }
}
