import React, { Component } from "react";
import ProjectsCreator from "./ProjectsCreator";
import axios from "axios";
import './Style.css';

class Projects extends Component {
  constructor() {
    super();
    this.state = {
      projectsData: [],
    };
  }

  componentDidMount() {
    axios.get("json/projects.json").then((res) => {
      this.setState({ projectsData: res.data });
    });

  }

  //fill with info

  projectsFiller() {
    let { projectsData } = this.state;
    return projectsData.map((data) => {
      return (
        <ProjectsCreator img={data.img} id={data.id} info={data.info} link={data.link} key={data.id}/>
      );
    });
  }

  render() {
    return (
      <section id="projects">
        <div className="row">
            <div className='midLine'></div>
          <h1 className="titleFontSize">Selected Projects</h1>
          <p className="normalFontSize">
            Digital design is like painting, except the paint never dries.
          </p>
          {this.projectsFiller()}
        </div>
      </section>
    );
  }
}

export default Projects;
