import React, { Component } from "react";
import axios from "axios";
import TeamCreator from "./TeamCreator";
import './Style.css';

class Team extends Component {
  constructor() {
    super();
    this.state = {
      teamData: [],
    };
  }

  componentDidMount() {
    axios.get("json/team.json").then((res) => {
      this.setState({ teamData: res.data });
    });
}

componentDidUpdate(){
  if(this.state.teamData.length === 1){
    document.getElementById('team_1').style.gridColumn = '1/-1';
  }
}
  //fill with info

  teamFiller() {
    let { teamData } = this.state;
    return teamData.map((data) => {
      if(data.facebook !== '' && data.facebook !== undefined){
        var facebookLogo = "bx bxl-facebook";
      }
      if(data.instagram !== '' && data.instagram !== undefined){
        var instagramLogo = "bx bxl-instagram";
      }
      if(data.behance !== '' && data.behance !== undefined){
        var behanceLogo = "bx bxl-behance";
      }
      return (
        <TeamCreator
          img={data.img}
          id={data.id}
          name={data.name}
          job={data.job}
          facebookLogo={facebookLogo}
          instagramLogo={instagramLogo}
          behanceLogo={behanceLogo}
          facebook={data.facebook}
          instagram={data.instagram}
          behance={data.behance}
          key={data.id}
        />
      );
    });
  }

  render() {
    return (
        <section id='team'>
            <div className='row'>
            <div className='midLine'></div>
                <h1 className='titleFontSize'>Meet Our Team</h1>
                <p className='normalFontSize'>Good design is not free, and free design is not good.</p>
                {this.teamFiller()}
                <div className='midLine'></div>
            </div>

    </section>
    );
  }
}

export default Team;
