import React from "react";
import "./Style.css";
import { HeroImg } from "./Style";

function Hero() {
  return (
    <HeroImg id="hero">
      <div className="row">
        <h1 className="titleFontSize">Say welcome to the creative world !</h1>
        <p className="normalFontSize">
          Good design's not about what meduim you're working in. It's about
          thinking hard about what you want to do and what you have to work with
          before you start.
        </p>
        <button className="btn">Getting Started Today</button>
      </div>
    </HeroImg>
  );
}

export default Hero;
