import React from "react";
import ErrorNav from "../NavBar/ErrorNav";
import Footer from "../Footer/Index";
import './Style.css';

function PageNotFound () {

        return(
            <React.Fragment>
                <ErrorNav/>
                <div id='pageNotFound'>
                    <img alt='page not found' className='pageNotFound__img' src='images/error404.svg'/>
                    <h1>ERROR 404</h1>
                    <h5>Page Not Found</h5>
                </div>
                <Footer/>
            </React.Fragment>
        );

}

export default PageNotFound;