import React from "react";
import { Link } from "react-router-dom";
import "./NavOpen.css"
function NavMobOpen (props){

    return(
        <div className='NavMobOpen'>
            <div className='navMobClose' onClick={() => props.setChanged()}><i className='bx bx-x'></i></div>
            <ul>
            <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <a href="#services">Services</a>
              </li>
              <li>
                <a href="#projects">Projects</a>
              </li>
              <li>
                <a href="#team">Team</a>
              </li>
              <li>
                <Link to="/syrian_vlogers">Syrian vloggers</Link>
              </li>
              <li>
                <a href="#contact">Contact</a>
              </li>
            </ul>
        </div>
    );
}

export default NavMobOpen;
