import React from "react";

function ServicesCreator (props) {

    return (
      <div className="services__container" id={props.id}>
        <div className="services__logo">
          <p><i className={props.logo}></i></p>
        </div>
        <div className="services__title">
          <p>{props.title}</p>
        </div>
        <div className="services__info">
          <p>{props.info}</p>
        </div>
      </div>
    );

}

export default ServicesCreator;
